#certs{
    font-family: 'Lobster';
    font-size: 100px;
    color: black; 
    padding-top: 50px; 
   
}


#certifications{
    font-family: 'Muli';
    font-size: 20px; 
    margin-top: 50px; 
    margin-bottom: 40px; 
    color: white;
   background-color: white;
    width: 100%;
    height: 250px;
    overflow-x: hidden;
    overflow-y: visible; 
   margin-left: auto;
   margin-right: auto; 
   border: 1px solid white;
}


::-webkit-scrollbar{
    background-color: lightgrey;
    width: 5px;
}
::-webkit-scrollbar-thumb:hover {
    background:lightpink; 
  }

  .link-out-text{
    display: block;
    color: #000;
    padding: 8px 16px;
    text-decoration: none;
  }
  
.link-out-text:hover:not(.active){ 
    background-color: pink;
    color: white;
  }
  

